import { useState, useEffect } from "react";
import { Text, Divider, Card, CardBody, Spacer } from "@chakra-ui/react";
import axios from "axios";
import { useParams } from "react-router-dom";

axios.defaults.withCredentials = true;

interface QuestionData {
  id: number;
  subject: string;
  content: string;
  createDate: [number, number, number, number, number, number, number]; //  
  authorUsername: string;
  modifyDate: string | null;
  answerList: any[]; //     (    )
}

export function Question() {
  const { id } = useParams<{ id: string }>(); // URL id  
  const [question, setQuestion] = useState<QuestionData | null>(null);

  useEffect(() => {
    // API  
    axios.get<QuestionData>(`${process.env.REACT_APP_API_URL}/questions/${id}`)
      .then(response => {
        setQuestion(response.data);
      })
      .catch(error => {
        console.error("Error fetching question:", error);
      });
  }, [id]);

  if (!question) {
    return <Text>Loading...</Text>; //     
  }

  const formatCreateDate = (createDate: [number, number, number, number, number, number, number]) => {
    const [year, month, day, hour, minute, second] = createDate;
    return new Date(year, month - 1, day, hour, minute, second).toLocaleDateString();
  };

  return (
    <>
      <Text>{question.subject}</Text>
      <Divider />
      <Card>
        <CardBody>
          <Text>{question.content}</Text>
          <Text>: {question.authorUsername}</Text>
          <Text> : {formatCreateDate(question.createDate)}</Text>
        </CardBody>
      </Card>
      <Spacer height={10} />
      <Text>   .</Text>
      {question.answerList.map((answer, index) => (
        <Card key={index}>
          <CardBody>
            <Text>{answer.content}</Text>
            <Text>: {answer.authorUsername}</Text>
            <Text> : {new Date(answer.createDate).toLocaleString()}</Text>
            
          </CardBody>
        </Card>
      ))}
    </>
  );
}
